/****************************************************************************************/
/***************************** c9 core block edits for client custom theme
/****************************************************************************************/
:root {
  font-family: 'helvetica neue', helvetica, sans-serif; }

:root {
  font-family: "helvetica", times, serif; }

.editor-styles-wrapper,
.editor-post-title__block .editor-post-title__input {
  font-family: "helvetica", times, serif !important; }

.editor-styles-wrapper ul li,
.editor-styles-wrapper ol li {
  font-size: 2rem;
  color: #4a4a4a; }

.editor-styles-wrapper .h1,
.editor-styles-wrapper .h2,
.editor-styles-wrapper .h3,
.editor-styles-wrapper .h4,
.editor-styles-wrapper .h5,
.editor-styles-wrapper .h6,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6 {
  font-family: "bebas-neue-by-fontfabric", "helvetica", times, serif;
  font-weight: 600; }

.editor-styles-wrapper p.wp-block-subhead,
.editor-styles-wrapper .subhead-h,
.editor-styles-wrapper .c9-sh,
.editor-styles-wrapper .c9-heading h1.subhead-h1,
.editor-styles-wrapper .c9-heading h2.subhead-h2,
.editor-styles-wrapper .c9-heading h3.subhead-h3,
.editor-styles-wrapper .c9-heading h4.subhead-h4,
.editor-styles-wrapper .c9-heading h5.subhead-h5,
.editor-styles-wrapper .c9-heading h6.subhead-h6,
.editor-styles-wrapper .c9-heading .h1 .text-muted,
.editor-styles-wrapper .c9-heading .h2 .text-muted,
.editor-styles-wrapper .c9-heading .h3 .text-muted,
.editor-styles-wrapper .c9-heading .h4 .text-muted,
.editor-styles-wrapper .c9-heading .h5 .text-muted,
.editor-styles-wrapper .c9-heading .h6 .text-muted {
  font-family: "wisdom-script", "helvetica", times, serif; }

.editor-styles-wrapper .c9-toggles .c9-toggles-item-heading .c9-toggles-label {
  font-family: "bebas-neue-by-fontfabric", "helvetica", times, serif !important; }

.editor-styles-wrapper .c9-h,
.editor-styles-wrapper .c9-h.h,
.editor-styles-wrapper .c9-txl,
.editor-styles-wrapper .display-1,
.editor-styles-wrapper .display-2,
.editor-styles-wrapper .display-3,
.editor-styles-wrapper .display-4,
.editor-styles-wrapper .display-5,
.editor-styles-wrapper .display-6 {
  line-height: .9 !important; }

.editor-styles-wrapper p.wp-block-subhead,
.editor-styles-wrapper .subhead-h,
.editor-styles-wrapper .c9-sh,
.editor-styles-wrapper .c9-heading h1.subhead-h1,
.editor-styles-wrapper .c9-heading h2.subhead-h2,
.editor-styles-wrapper .c9-heading h3.subhead-h3,
.editor-styles-wrapper .c9-heading h4.subhead-h4,
.editor-styles-wrapper .c9-heading h5.subhead-h5,
.editor-styles-wrapper .c9-heading h6.subhead-h6 {
  font-family: "wisdom-script", "helvetica", times, serif;
  font-weight: 700; }

/* custom tabs */
.editor-styles-wrapper .c9-horizontal-tabs .nav-tabs .nav-link.active,
.editor-styles-wrapper .woocommerce div.product .woocommerce-tabs ul.tabs li.active a.nav-link {
  background-color: #FCBD13;
  padding: 13px 55px; }

.editor-styles-wrapper .c9-horizontal-tabs ul.nav.nav-tabs {
  margin: 30px 0px 0px 0px !important; }

/* buttons */
.editor-styles-wrapper .c9-block-cta .c9-cta-content .wp-block-button__link {
  font-size: 30px; }

/* search widget block */
.wp-block-search .wp-block-search__button .wp-block-search__button-rich-text {
  padding: 0px;
  margin: 0px;
  color: #000;
  font-size: 30px; }

.editor-styles-wrapper .wp-block-search .wp-block-search__button {
  font-weight: 600; }

/* grids inside of grids inside of tabs */
@media only screen and (min-width: 1200px) {
  body:not(.folded) .edit-post-layout.is-sidebar-opened .editor-styles-wrapper .wp-block[data-type="c9-blocks/grid"] .container .c9-grid .container,
  body.folded .edit-post-layout.is-sidebar-opened .editor-styles-wrapper .wp-block[data-type="c9-blocks/grid"] .container .c9-grid .container {
    width: auto;
    min-width: 600px; } }

